body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.calendar {
  width: 30%;
  margin: auto;
}

h1 {
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.selectorBar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}
.yearSelector {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.monthSelector {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.month {
    display:-webkit-flex;
    display:flex;
}
.table {
    table-layout: fixed;
    width: 100%;
    border: 1px solid black;
}
th {
    text-align: left;
}

.day {
    display: block;
    width: 100%;
    min-height: 50px;
}
.planner {
    border-style: solid;
    border-width: 1px;
    margin: 10px 0px 10px 0px;
    padding: 10px;
}

.title {
    font-weight: bold;
    font-size: 20px;
}

.heading {
    font-weight: bold;
    font-size: 16px;
}

.form {
    border-style: solid;
    border-width: 1px;
    padding: 10px;
}

input {
    margin-left: 1em;
}

textarea{
    width: 99%;
    height: 150px;
    resize: none;
}
.plan {
    border-style: solid;
    border-width: 1px;
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.info {
    padding-top: 5px;
    padding-bottom: 5px;
}

.button {
    margin-top: 5px;
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
}
